$text-card-color: #616070;

.card {
  display: inline-block;
  font-size: 12px;
  border: 1px solid #f3f3f3;
  color: $main-color;
  padding: 32px 16px 16px;

  &__image {
    @include flex(null);
    padding: 0 3px;
    margin-bottom: 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    font-weight: 500;
    max-width: 166px;
    margin-bottom: 4px;
    line-height: 18px;
  }

  &__code {
    @include flex(null);
    color: $text-card-color;
    font-size: 10px;
    line-height: 14px;
    margin-bottom: 16px;
  }

  &__feedback {
    @include flex (space-between);
    align-items: center;
    font-size: 10px;
    margin-bottom: 24px;
  }

  &__price {
    @include flex (space-between);
    align-items: center;
    margin-bottom: 16px;
    line-height: 18px;

    &-text {
      color: $text-card-color;
    }

    &-value {
      font-size: 16px;
      font-weight: bold;
    }
  }

  &__button {
    @include flex (center);
    text-decoration: none;
    flex-grow: 1;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    padding: 11px 0;
    font-size: 14px;
    border: 1px solid transparent;
    border-radius: 5px;
    background-color: $link-color;

    &:hover {
      background-color: #fff;
      border-color: $link-color;
      color: $link-color;
    }
  }
}
